<template>
  <div>
    <Histogram
      :chartId="setUniqueChartId(filter.key)"
      :data="filter"
      :currMin="currMin"
      :currMax="currMax"
    />

    <!--    debug {{ range }}-->

    debug {{ currMin }}

    <!--    Debug step size {{ getDynamicStepSize(filter) }}-->

    <v-row>
      <!--      debug step="0.01" helps handle decimal values-->
      <!--      thumb size defaults to 32 can be changed-->
      <v-range-slider
        :min="filter.min"
        :max="filter.max"
        :step="stepSize"
        :value="range"
        @input="setActiveRange"
      >
      </v-range-slider>
    </v-row>

    <v-row class="mt-0">
      <v-col class="pt-0">
        <v-text-field
          v-model="currMin"
          class="pt-0"
          label="Min"
          type="number"
          :rules="[
            (value) => value >= absMin || `Min has to be at least ${absMin}`,
          ]"
          @blur="updateMinRange"
          @keyup.enter="updateMinRange"
        ></v-text-field>
      </v-col>
      <v-col class="pt-0">
        <v-text-field
          v-model="currMax"
          class="pt-0"
          label="Max"
          type="number"
          :rules="[
            (value) => value <= absMax || `Max has to be at the most ${absMax}`,
          ]"
          @blur="updateMaxRange"
          @keyup.enter="updateMaxRange"
        ></v-text-field>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Histogram from "@/components/Histogram";
export default {
  name: "ResultsFilter",
  components: {
    Histogram,
  },
  props: ["filter"],
  data: () => ({
    currMin: null,
    currMax: null,
    absMin: null,
    absMax: null,
    range: [],
    isGrabbingSlider: false,
    isFinishedInitFilter: false,
    stepSize: 1,
  }),
  methods: {
    setActiveRange(newRange) {
      this.range = newRange;
      if (!this.isFinishedInitFilter) return;

      this.$emit("activeRangeChanged", {
        key: this.filter.key,
        activeRange: this.range,
      });
    },
    updateMaxRange() {
      // Vuetify v-range-slider caveat forces to pass a full array with 2 values to detect change.
      this.range = [this.range[0], parseInt(this.currMax)];
      this.$emit("activeRangeChanged", {
        key: this.filter.key,
        activeRange: this.range,
      });
    },
    updateMinRange() {
      // Vuetify v-range-slider caveat forces to pass a full array with 2 values to detect change.
      this.range = [parseInt(this.currMin), this.range[1]];
      this.$emit("activeRangeChanged", {
        key: this.filter.key,
        activeRange: this.range,
      });
    },
    setUniqueChartId(key) {
      const cleanKey = key
        .toLowerCase()
        .replace(" ", "")
        .replace(/[^\w\s]/gi, "");
      return `engora-filter-${cleanKey}`;
    },
    initAbsLimitsAndDynamicStepSize() {
      // Given bases 0.1 and 0.5 we find a step size <base value>*Math.pow(10, n)
      // Which gives us approximately n_steps steps
      // for round-looking numbers with a fairly smooth slider action and an inclusive range
      let n_steps = 1000;
      let exact_step = (this.filter.max - this.filter.min) / n_steps;
      let bases = [0.5, 0.1];
      let sample_step = bases[0];
      let steps = (this.filter.max - this.filter.min) / sample_step;
      if (steps < n_steps) {
        while (sample_step > exact_step) {
          sample_step = bases[bases.length - 1];
          bases.push(bases[bases.length - 2] / 10.0);
        }
      } else {
        while (sample_step < exact_step) {
          sample_step = bases[bases.length - 1];
          bases.push(bases[bases.length - 2] * 10.0);
        }
      }
      let step = sample_step;
      let lower_limit = Math.floor(this.filter.min / step) * step;
      lower_limit = parseFloat(lower_limit.toFixed(10));
      if (lower_limit > this.filter.min) {
        lower_limit = lower_limit - step;
      }
      let upper_limit = Math.ceil(this.filter.max / step) * step;
      upper_limit = parseFloat(upper_limit.toFixed(10));
      if (upper_limit < this.filter.max) {
        upper_limit = upper_limit + step;
      }

      this.currMin = lower_limit;
      this.currMax = upper_limit;
      this.absMin = lower_limit;
      this.absMax = upper_limit;
      this.range = [lower_limit, upper_limit];
      this.stepSize = step;
    },
  },
  beforeMount() {
    this.initAbsLimitsAndDynamicStepSize();
  },
  mounted() {
    this.isFinishedInitFilter = true;
  },
};
</script>

<style lang="scss">
.v-slider__thumb {
  cursor: pointer;
}
</style>
