<template>
  <div>
    <v-container class="pt-0">
      <v-expansion-panels :value="panels" multiple>
        <v-expansion-panel
          v-for="(filter, i) in filters"
          :key="i"
          @change="panelChanged(i)"
        >
          <v-expansion-panel-header>
            <div class="column">
              <div class="body-1 font-weight-bold">{{ filter.key }}</div>
              <div>
                <span class="body-2 mr-2">Min: {{ filter.min }}</span>
                <span class="body-2">Max: {{ filter.max }}</span>
              </div>
            </div>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <!--            <span>{{ filter.values }}</span>-->

            <FilterItem
              :filter="filter"
              @activeRangeChanged="activeRangeChanged"
            />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-container>
  </div>
</template>

<script>
import FilterItem from "@/components/FilterItem";
export default {
  name: "Filters",
  props: ["panels", "filters"],
  components: {
    FilterItem,
  },
  methods: {
    panelChanged(i) {
      this.$emit("panelChanged", i);
    },
    activeRangeChanged(filter) {
      this.$emit("activeRangeChanged", filter);
    },
  },
};
</script>
